<script>
import { createNamespacedHelpers } from "vuex";
const { mapState } = createNamespacedHelpers("likeMovieModule");
export default {
  name: "home",
  data() {
    return {
      activeName: "a",
      page: false,
      books: false
    };
  },
  methods: {
    push(o) {
      this.$router.push(o);
    }
  },
  computed: { ...mapState(["presentuser"]) },
  created() {
    //验证用户登录状态
    let user = localStorage.getItem("user");
    if (user) {
      this.books = true;
      let likeuser = JSON.parse(user);
      if (likeuser.isLogin) {
        //获取所有用户上架的书籍
        let likebooks = localStorage.getItem("likebooks");
        likebooks = likebooks ? JSON.parse(likebooks) : {};
        //获取当前用户的数据
        let presentuser = likebooks[likeuser.username];
        presentuser = presentuser ? presentuser : [];
        //判断用户书籍数据长度
        if (presentuser.length == 0) {
          this.page = true;
        }
        //生成书架展示数据
        //生成页面数据
        this.$store.commit(
          "likeMovieModule/getCurrentLikeMovieData",
          presentuser
        );
      }
    } else {
      //书架出现提示信息
      this.page = true;
      this.books = false;
    }
  }
};
</script>
